import React, {useContext, useState} from 'react';
import {NavLink, useHistory} from 'react-router-dom';
import './Nav.css';
import {MovieContext} from './MovieContext';

function Nav({toggleIsUserOpen}) {
    let history = useHistory();
    const [movies, setMovies] = useContext(MovieContext);
    const [isPreferenceOpen, toggleIsPreferenceOpen] = useState(false);

    return (
        <div className='nav-container'>
            <header>
              <h1>
                <i className="fas fa-video"></i>
                <span>Movie Center</span>
              </h1>

              <nav>
                  <ul>
                      <li><NavLink exact to="/" activeClassName="link-active">Home</NavLink></li>
                      <li><NavLink to="/about" activeClassName="link-active">About</NavLink></li>
                      <li><NavLink to="/signin" activeClassName="link-active">{movies.isLogin ? 'Sign Out' : 'Sign In'}</NavLink></li>
                  </ul>
              </nav>

              <div className='btn-wrapper'>
                <button onClick={()=>toggleIsPreferenceOpen(prev=>!prev)} className='btn-wrapper-controller'>Preference<i className="fas fa-arrow-circle-down"></i></button>
                <div className={`btn-wrapper-list ${isPreferenceOpen ? 'open':''}`}>
                  <button style={{'--btn-index':1}} onClick={()=>{toggleIsPreferenceOpen(prev=>!prev); history.push('/history')}}>History</button>
                  <button style={{'--btn-index':2}} onClick={()=>{toggleIsPreferenceOpen(prev=>!prev); history.push('/favorites')}}>Favorite</button>
                </div>
              </div>

              <p>
                <span onClick={()=>history.push('/cart')} ><i className="fas fa-shopping-cart"></i></span>
                <span onClick={()=>toggleIsUserOpen()}><i className="fas fa-user"></i></span>
              </p>
            </header>
        </div> 
    )
}

export default Nav
